body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
	font-family: 'icomoon';
	src:  url(/be-smart-playlist/static/media/icomoon.46eab400.eot);
	src:  url(/be-smart-playlist/static/media/icomoon.46eab400.eot#iefix) format('embedded-opentype'),
		url(/be-smart-playlist/static/media/icomoon.77abe20e.ttf) format('truetype'),
		url(/be-smart-playlist/static/media/icomoon.f0a541bd.woff) format('woff'),
		url(/be-smart-playlist/static/media/icomoon.2bb84427.svg#icomoon) format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	-webkit-font-feature-settings: normal;
	        font-feature-settings: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

